import React, { Component } from 'react';
import Navigation from './Navigation';
import ShowPokemon from './ShowPokemon';
import ShowDetail from './ShowDetail';
import ShowCards from './ShowCards';
import ShowCardDetail from './ShowCardDetail';

import { Container, Row, Col, CardDeck } from 'reactstrap';


class View extends Component {
    constructor(props) {
      super(props);

        // getinitialState
        this.state = {
            currentView: "regions",
            currentRegion: "Kanto",
            pokeLoaded: false,
            pokemon: {},
            currentPokemon: {},
            currentCards: {},
            currentCard: null,
            cardIsLoaded: true,
            cardError: null,
            detailShowCritter: true,
        };

        this.changeView = this.changeView.bind(this);
        this.changeRegion = this.changeRegion.bind(this);
        this.getPokemon = this.getPokemon.bind(this);
        this.clickPokeName = this.clickPokeName.bind(this);
        this.getCards = this.getCards.bind(this);
        this.clickCard = this.clickCard.bind(this);

        let activeName;

        // this.makeSearchObj = this.makeSearchObj.bind(this);
        // this.updateRegionState = this.updateRegionState.bind(this);
    }

    getPokemon(){
        console.log("getPokemon", this.state.currentView, this.state.currentRegion);
        let url;
        if (this.state.currentView === "regions"){
            // console.log("url", `https://bell-pokemon.firebaseio.com/regional.json?orderBy="regionName"&equalTo="${this.state.currentRegion}"`);
            //look in regional
            url = `https://bell-pokemon.firebaseio.com/regional.json?orderBy="regionName"&equalTo="${this.state.currentRegion}"`
        }else if (this.state.currentView === "a-z"){
            url = "https://bell-pokemon.firebaseio.com/allPokemon.json"
        }
        fetch(url)
        .then(res => res.json())
        .then(
           (result) => {
            //   if (this.state.currentView === "regions") {
                //for regional
               //aphabatize and add fbID
               let newArray;
               if (this.state.currentView === "regions"){
                    newArray = Object.keys(result).map((key, index) => {
                        result[key].fbid = key;
                        return result[key];
                    });

                    newArray.sort(function(a, b) {
                        var textA = a.pName;
                        var textB = b.pName;
                        return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
                    });
                }else if (this.state.currentView === "a-z"){
                    newArray = Object.keys(result).map((key, index) => {
                        result[key].fbid = key;
                        return result[key];
                    });
                    newArray.sort(function(a, b) {
                        var textA = a.name;
                        var textB = b.name;
                        return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
                    });

                }
               //need to write for a-z or all pokemon

            this.setState({
               pokeLoaded: true,
               pokemon: newArray,
              });
           },
           // Note: it's important to handle errors here
           // instead of a catch() block so that we don't swallow
           // exceptions from actual bugs in components.
           (error) => {
               console.log("error", error);
                this.setState({
                    isLoaded: true,
                    error: error
                });
            }
        );
    }

    componentDidMount() {
        //lifecycle hook
        console.log("componentDidMount");
        this.getPokemon();
     }

    changeView(event){
       console.log("changeView event", event.target.id);

        this.setState({
         currentView: event.target.id,
         pokeLoaded: false,
         pokemon: {},
         error: null,
        }, this.getPokemon);
    }

    changeRegion(event){
       console.log("changeRegion", event.target.id);

       this.setState( {
          currentRegion: event.target.id,
          pokeLoaded: false,
          pokemon: {},
          error: null,
       }, this.getPokemon);

    }



    makeSearchObj(){
        console.log("makeObj", this.state.currentRegion);
        let searchObj = {
            currentView: this.state.currentView,
            currentRegion: this.state.currentRegion,
        }
        return searchObj;
    }

    getCards(){
        console.log("getCards", this.state.currentPokemon);
        let url = `https://api.pokemontcg.io/v1/cards?name=${this.state.currentPokemon.name}`;
        fetch(url)
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result);

                this.setState({
                    currentCards: result.cards,
                });
            },
        (error) => {
            console.log("error", error);
             this.setState({
                 cardIsLoaded: true,
                 cardError: error
             });
        });
    }



    clickPokeName(whichOne){
        let url = `https://bell-pokemon.firebaseio.com/allPokemon.json?orderBy="slug"&equalTo="${whichOne}"`;
        fetch(url)
        .then(res => res.json())
        .then(data => {
           console.log("the data of clickpokeName", data);
            //need to get data out of key

            let key = Object.keys(data)[0];
            data[key].fbID = key;
            this.setState({
                currentPokemon: data[key],
                detailShowCritter: true,
                currentCard: null,
            }, this.getCards);
        })
        .catch(err => console.log(err));
    }

    clickCard(whichOne){
        console.log("whichOne", whichOne);
        this.setState({
            detailShowCritter: false,
            currentCard: whichOne,
        });
    }

    render(){
        const { currentView, currentRegion, pokemon, pokeLoaded, currentPokemon, currentCards, currentCard, detailShowCritter} = this.state;
        let showDetail;
        let showCards;

        if (detailShowCritter){
            if (currentPokemon.name){
                showDetail = <ShowDetail currentPokemon={currentPokemon} col-6 />;
            }
        } else {
            if (currentCard){
                showDetail = <ShowCardDetail img={currentCard} currentPokemon={currentPokemon} />
            }
        }

        if (currentCards.length > 0){
            showCards = <ShowCards cards={currentCards} clickCard={this.clickCard} />
        }
        console.log("got here", currentView);
        return (
            <div >
                <Navigation
                    currentView={currentView}
                    changeView={this.changeView}
                    currentRegion={currentRegion}
                    changeRegion={this.changeRegion}
                    pokeLoaded={this.pokeLoaded} />
                    <Container fluid>
                    <Row>
                    <Col xs="2 poke-list">
                        {/*<ShowPokemon whichView={currentView} searchObj={searchObj}/>*/}
                        <ShowPokemon pokemon={pokemon}  currentView={currentView} clickPokeName={this.clickPokeName} />
                    </Col>
                    <Col xs="6 poke-details">
                        {showDetail}
                    </Col>
                    <Col xs="4 poke-cards">
                        {showCards}
                    </Col>
                    </Row>
                    </Container>
            </div>
        );
    }
}

export default View;


